<template>
  <div class="box">
    <div class="title">kr-print-designer </div>
    <section>
      <div class="section"></div>
      <el-table :data="tempList" style="width: 100%" size="mini" border>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.type == 1 ? '' : ''}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.width + ' * '+ scope.row.height}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.pageWidth + 'mm * '+ scope.row.pageHeight+'mm'}}</span>
          </template>
        </el-table-column>
        <el-table-column align="right">
          <template slot="header">
            <el-button size="mini" type="primary" @click="openCreate"></el-button>
          </template>
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button type="text" size="mini" @click="handlePreview(scope.$index, scope.row)"></el-button>
            <el-button type="text" size="mini" @click="handleCopy(scope.row)"></el-button>
            <el-button
              v-if="!scope.row.default"
              type="text"
              size="mini"
              @click="handleDelete(scope.$index, scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="visible" width="310px">
        <el-form ref="form" :model="form" size="small" :rules="rules" label-width="80px">
          <el-form-item label="" prop="title">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select v-model="form.type">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="width">
            <el-input v-model="form.width"></el-input>
          </el-form-item>
          <el-form-item label="" prop="height">
            <el-input v-model="form.height"></el-input>
          </el-form-item>
          <el-form-item label="" prop="pageWidth">
            <el-input v-model="form.pageWidth"></el-input>
          </el-form-item>
          <el-form-item label="" prop="pageHeight">
            <el-input v-model="form.pageHeight"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" @click="visible = false"> </el-button>
          <el-button type="primary" size="mini" @click="savaTemp"> </el-button>
        </div>
      </el-dialog>
    </section>
    <section>
      <div class="section"></div>
      <el-form ref="printForm" :model="printForm" size="small" :rules="printRules" :inline="true">
        <el-form-item label="" prop="tempIndex">
          <el-select v-model="printForm.tempIndex">
            <el-option
              v-for="(item,index) in tempList"
              :key="index"
              :label="item.title"
              :value="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="data">
          <el-input
            type="textarea"
            style="width:520px"
            :rows="6"
            v-model="printForm.data"
            placeholder="json"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <div>
            <el-button size="mini" type="primary" @click="printPreview(1)"></el-button>
          </div>
          <div style="margin-top:10px">
            <el-button size="mini" type="success" @click="printPreview(2)"></el-button>
          </div>
        </el-form-item>
      </el-form>
    </section>
    <section>
      <el-collapse>
        <el-collapse-item title="" name="1">
          <el-form ref="testData" :model="testData" size="small" :inline="true">
            <el-form-item label="">
              <el-input
                type="textarea"
                style="width:920px"
                :rows="8"
                v-model="testData.OutStock"
                placeholder="json"
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                type="textarea"
                style="width:920px"
                :rows="8"
                v-model="testData.InStock"
                placeholder="json"
              ></el-input>
            </el-form-item>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </section>
  </div>
</template>
<script>
import { Table, TableColumn, Dialog, MessageBox } from 'element-ui'
import Vue from 'vue'
Vue.prototype.$confirm = (...args) => MessageBox.confirm(...args)
const defaultTemp = () => ({
  title: '',
  type: 1, //  12
  width: '',
  height: '',
  pageWidth: '',
  pageHeight: '',
  tempItems: [],
})
import { OutStockData, InStockData } from '../data/test'
export default {
  data() {
    return {
      visible: false,
      value: {},
      tempList: [],
      form: defaultTemp(),
      rules: {
        title: { required: true, message: '' },
        type: { required: true, message: '' },
        width: { required: true, message: '' },
        height: { required: true, message: '' },
        pageWidth: { required: true, message: '' },
        pageHeight: { required: true, message: '' },
      },
      printForm: {
        tempIndex: '',
        data: '',
      },
      printRules: {
        tempIndex: { required: true, message: '' },
        data: { required: true, message: '' },
      },
      testData: {
        OutStock: JSON.stringify(OutStockData),
        InStock: JSON.stringify(InStockData),
      },
    }
  },
  created() {
    Vue.use(Table)
    Vue.use(TableColumn)
    Vue.use(Dialog)
    this.tempList = JSON.parse(localStorage.getItem('tempList')) || []
  },

  methods: {
    // 
    handlePreview(index, row) {
      this.$lodop.previewTemp(row)
    },
    // 
    handleEdit(index, row) {
      this.$router.push({
        path: '/designer',
        query: {
          index: index,
        },
      })
    },
    // 
    handleDelete(index, row) {
      this.$confirm('', '')
        .then((isPass) => {
          if (isPass) {
            this.tempList.splice(index, 1)
            localStorage.setItem('tempList', JSON.stringify(this.tempList))
          }
        })
        .catch()
    },
    handleCopy(row) {
      let copyTemp = JSON.parse(JSON.stringify(row))
      copyTemp.title = copyTemp.title + '_copy'
      copyTemp.default = false
      this.tempList.push(copyTemp)
      localStorage.setItem('tempList', JSON.stringify(this.tempList))
    },

    // 
    openCreate() {
      this.form = defaultTemp()
      this.visible = true
    },
    // 
    savaTemp() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.tempList.push(this.form)
          localStorage.setItem('tempList', JSON.stringify(this.tempList))
          this.visible = false
        }
      })
    },
    /**
     * /
     * flag 1:2
     */
    printPreview(flag) {
      this.$refs.printForm.validate((valid) => {
        if (valid) {
          let printData
          try {
            printData = JSON.parse(this.printForm.data)
          } catch (err) {
            return this.$message('')
          }
          if (flag == 1) {
            this.$lodop.preview(this.tempList[this.printForm.tempIndex], printData)
          } else {
            this.$confirm('', '')
              .then((isPass) => {
                if (isPass) {
                  this.$lodop.print(this.tempList[this.printForm.tempIndex], printData)
                }
              })
              .catch((err) => {
                console.log(err)
              })
          }
        }
      })
    },
  },
}
</script>
<style scoped>
.box {
  width: 1000px;
  margin: auto;
  padding: 10px 0;
  font-family: Arial, Helvetica, sans-serif;
}
.title {
  text-align: center;
  font-size: 24px;
  color: #1890ff;
  margin: 20px;
  font-weight: bold;
}
.section {
  font-size: 18px;
  color: #333333;
  padding: 10px 0;
}
</style>
