<template>
  <div>
    <!--  -->
    <el-form label-width="80px" :model="activeElement" size="mini" class="kr-form">
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              v-model="activeElement.width"
              controls-position="right"
              :min="1"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              v-model="activeElement.height"
              controls-position="right"
              :min="1"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              v-model="activeElement.left"
              controls-position="right"
              :min="0"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              v-model="activeElement.top"
              controls-position="right"
              :min="0"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              :disabled="activeElement.style.FontSize===undefined"
              v-model="activeElement.style.FontSize"
              controls-position="right"
              :min="0"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-switch
              :disabled="activeElement.style.Bold===undefined"
              v-model="activeElement.style.Bold"
              :active-value="true"
              :inactive-value="false"
            ></el-switch>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8">
          <el-form-item label="">
            <el-color-picker
              :disabled="activeElement.style.FontColor===undefined"
              v-model="activeElement.style.FontColor"
            ></el-color-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-color-picker
              :disabled="activeElement.style.BorderColor===undefined"
              v-model="activeElement.style.BorderColor"
            ></el-color-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-switch
              :disabled="activeElement.style.ShowBarText===undefined"
              v-model="activeElement.style.ShowBarText"
              :active-value="true"
              :inactive-value="false"
            ></el-switch>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-form-item label="">
          <el-radio-group
            :disabled="activeElement.style.Alignment===undefined"
            v-model="activeElement.style.Alignment"
          >
            <el-radio-button :label="'left'"></el-radio-button>
            <el-radio-button :label="'center'"></el-radio-button>
            <el-radio-button :label="'right'"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-select
              :disabled="activeElement.style.codeType===undefined"
              v-model="activeElement.style.codeType"
              class="min-input"
            >
              <el-option v-for="val in codeTypeArray" :key="val" :label="val" :value="val" />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-select
              :disabled="activeElement.style.ItemType===undefined"
              v-model="activeElement.style.ItemType"
              class="min-input"
            >
              <el-option
                v-for="val in itemTypeArray"
                :key="val.value"
                :label="val.label"
                :value="val.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-switch
              :disabled="activeElement.style.AutoHeight===undefined"
              v-model="activeElement.style.AutoHeight"
              :active-value="true"
              :inactive-value="false"
            ></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input-number
              :disabled="activeElement.style.BottomMargin===undefined || !activeElement.style.AutoHeight"
              v-model="activeElement.style.BottomMargin"
              controls-position="right"
              :min="0"
              class="min-input"
            ></el-input-number>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { getCodeTypeArray, getItemTypeArray } from '../../libs/props.js'

export default {
  data() {
    return {
      codeTypeArray: getCodeTypeArray(),
      itemTypeArray: getItemTypeArray(),
    }
  },
  computed: {
    activeElement() {
      return this.$vptd.state.activeElement
    },
    // 
    height() {
      return this.$vptd.state.page.height
    },
  },
}
</script>
<style lang="scss" scoped>
.mini-form {
  ::v-deep.el-form-item--mini.el-form-item {
    margin-bottom: 10px;
  }
}
</style>
