<template>
  <div
    class="widgets"
    v-html="val.value"
    :contenteditable="!!val.isEdit"
    @blur="(e) => updateText(e, val.uuid)"
    :style="{
      position:'absolute',
      left: val.left + 'px' ,
      top:  val.top + 'px',
      width: val.width + 'px',
      minHeight: val.height + 'px',
      zIndex: val.style.zIndex,
      fontSize: val.style.FontSize + 'pt',
      color: val.style.FontColor,
      textAlign: val.style.Alignment,
      fontWeight: val.style.Bold ? 'bold': 'normal'
    }"
  ></div>
</template>

<script>
// let itemAttr = {
//   type: 'braid-txt',
//   isEdit: 0,
//   title: '',
//   value: '{}',
//   defaultValue: '18982068702',
//   name: 'buyerPhone'
// }

const WIDGET_NAME = 'braid-txt'

export default {
  name: WIDGET_NAME,
  setting: {
    type: WIDGET_NAME,
    isEdit: true,
    dragable: true, // 
    resizable: true, // 
    width: 120,
    height: 40,
    left: 50,
    top: 0,
    title: '',
    value: '',
    defaultValue: '',
    name: '',
    style: {
      zIndex: 0,
      FontSize: 9,
      FontColor: '#  000000',
      Bold: false, // 10
      Italic: false, // 10
      Underline: false, // 10
      Alignment: 'left', // 
      ItemType: 0, //  0-- 1-- 2-- 3-- 4--
    },
  },
  props: [
    'val', // 
  ],
  methods: {
    updateText(e, uuid) {
      let text = e.target.innerHTML
      this.$vptd.commit('updateData', {
        uuid: uuid,
        key: 'value',
        value: text,
      })
    },
  },
}
</script>

