<template>
  <div
    class="widgets"
    :style="{
      left: val.left + 'px' ,
      top:  val.top + 'px',
      width: val.width + 'px',
      height: val.height + 'px',
      textAlign: val.style.Alignment,
      fontSize: val.style.FontSize + 'pt',
      color: val.style.FontColor,
    }"
    style="position:absolute;overflow:hidden"
  >
    <table
      border="1"
      width="100%"
      cellspacing="0"
      cellpadding="2"
      style="border-collapse:collapse;font-size:12px;"
      bordercolor="#000000"
    >
      <tr>
        <th
          v-for="item in columns"
          :key="item.name"
          :width="item.name==='_seq'?40:''"
        >{{item.title}}</th>
      </tr>
      <tr>
        <td v-for="item in columns" :key="item.name">{{item.value}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
const WIDGET_NAME = 'braid-table'

export default {
  name: WIDGET_NAME,
  setting: {
    type: WIDGET_NAME,
    isEdit: false, // 
    dragable: true, // 
    resizable: true, // 
    width: 240,
    height: 60,
    left: 50,
    top: 10,
    title: '',
    value: [],
    defaultValue: [],
    tabelHtml: '',
    columnsAttr: [], // 
    columns: [], // 
    selectCol: [], // name
    name: '',
    style: {
      zIndex: 0,
      Alignment: 'left', //  1-- 2-- 3--
      FontSize: 9,
      FontColor: '#000000',
      BorderColor: '#000000',
      AutoHeight: false, // 
      BottomMargin: 0, // 
    },
  },
  props: [
    'val', // 
  ],
  computed: {
    // type='row'
    columns() {
      let col = this.val.columns || []
      return col
    },
  },
  methods: {},
}
</script>

